import { Base } from './base.model';
import { Cargo } from './cargo.model';
import { CentroTreinamento } from './centro-treinamento.model';
import { Curriculum } from './currriculum.model';
import { GrupoEspecializado } from './grupo-especializado.model';

export class CurriculumProfissional extends Base {

    public curriculum?: Curriculum;
    public centro_treinamento?:CentroTreinamento;
    public cargo?:Cargo;
    public grupo_especializado?:GrupoEspecializado;

    public ano_ingresso: string = ""; //Ano de ingresso na instituio
    public lotacao_atual: string = "";//Lotao atual do servidor
    public pgd_inserido: string = ""; // Esta ou no inserido no PGD da instituio e qual modalidade
    public pgd_interesse: string = "";//Tem interesse ou no no PGD da instituio e qual modalidade
    public telefone: string = "";// Telefone do chefe imediato caso tenha interesse no PGD
   
    public funcoes: [] = [];// Funes ocupadas em toda carreira como titular ou substituto
    public unidades_lotado: [] = [];// Unidades de lotao em toda carreira
    public atividades_fora: [] = [];//Atividades desempenhadas externamente que podem contribuir para intituio
    public atividades_internas: [] = [];//Atividades desempenhadas internamente que podem contribuir para intituio
    public especifique_habilidades: [] = [];//Atividades desempenhadas internamente que podem contribuir para intituio
    public docencia_fora: [] = [];// J foi docente fora da instituio
    public docencia_interna: [] = [];// J foi docente na instituio
    public curso_fora: [] = [];//Quais cursos voc j fez e quais pretende fazer fora da Instituio
    public curso_interno: [] = [];//Quais os principais cursos que voc j fez e pretende fazer na Instituio
   
    public viagem_nacional : number = 0;//J fez viagem nacional a trabalho
    public viagem_internacional: number = 0; //J fez viagem internacional a trabalho
    public interesse_bnt: number = 0; //Voc tem interesse na participao do Banco Nacional de Talentos -PRF
    public remocao: number = 0; // Tem interesse em remoo
        
    public curriculum_id? : string= ""; //ID Curriculum
    public centro_treinamento_id: string = ""; //ID do CT 
    public cargo_id: string = ""; //ID do Cargo
    public grupo_especializado_id: string = ""; //ID do Grupo Especializado
            
    public constructor(data?: any) { super(); this.initialization(data); }
}
